
//   ---------------- swiper   hotel-rooms   hotel-page----------------------
.hotel-rooms__slider {
	&.swiper {
		//  
	width: 290px;
	margin-bottom: 20px; 

	@media screen and (min-width: $mobile) and (max-width: 1023px) {
		width: 404px;
		left: -135px;	
	}

	@include tablet {
		//  
		width: 428px;
		margin: 0;
	}
	@include desktop {
		//  
		width: 548px;
	}
	}	
}

.hotel-rooms__item:nth-child(2n) {
	.swiper {
		@media screen and (min-width: $mobile) and (max-width: 1023px)  {
			left: 120px;
		}
	}	
}

//  
.swiper-btn__prev,
.swiper-btn__next {
	width: 32px;
	height: 32px;
	bottom: 0;	
}

//        #f3c653;
.swiper-btn__prev:hover .icon-arrow,
.swiper-btn__next:hover .icon-arrow {
	fill: $accent-color;
}

//   
.swiper-btn__list {
	position: relative;
	display: flex;
	gap: 32px;
	width: 96px;
	
	//  320px  mobile   
	@media screen and (max-width: 413px){
		margin: 0 auto;
	}

	@media screen and (min-width: $mobile) and (max-width: 1023px){
		left: 135px;
	}

	@include tablet {
		margin-left: auto;
	}

}

//       ( )
//    
.hotel-rooms__item:nth-child(2n) {
	.swiper-btn__list {
		margin: 0 auto;

	@media screen and(min-width: $mobile) and (max-width: 1023px) {
		left: 15px;
	}

	@include tablet {
		margin: 0;
	}
	}
  }

//   
.hotel-rooms__slider .swiper-wrapper {
	margin-bottom: 20px;
}
